.messages{
    background-color: white;
    margin-top: 20px;
    padding: 5px;
    min-height: 470px;
    max-height: 470px;
    scroll-behavior: auto;
}
.message{
    background-color: lavender;
    margin-bottom: 20px;
    padding: 10px;
    width: 45px auto;

}
.message :active{
    background-color: lavender;
    color: lightcoral;
    margin-bottom: 20px;
    padding: 10px;
    width: 45px auto;
}





.new {
    margin-left: 600px;
    min-width: 90px;
    min-height: 50px;
    max-width: 150px;
    height: auto;
    width: auto;
    
}
.newText {
    word-wrap: break-word;
    min-width: 80px;
    min-height: 25px ;
    border-radius: 8px;
    padding: 5px;
    font-size: 15px;
    background-color: gainsboro;
    text-align: left;
}
.time {

    font-size: 10px;
    width: 20px;
    height: 15px;
    margin-left: 120px;
    text-align: right;
}


.area{
    position: relative;
    top: 430px;
    margin: 0px 0px 0px 0px;
    width: 90%;
    height: 30px;
    resize: none;
    border: blue 2px double;
    

}
.button {
    position: relative;
    top: 415px;
    width: 7.5%;
    height: 38px;
    
}